      <p>姓&emsp;&emsp;名：潘薇艳</p>
      <p>学&emsp;&emsp;历：本科</p>
      <p>年&emsp;&emsp;龄：25</p>
      <p>电&emsp;&emsp;话：178 0000 0000</p>
