      margin: 0 auto;
      padding: 20px;
      width: 170px;
      height: 170px;
      border-radius: 100%;
    }
    .brief_introduction{
      padding-left: 15px;
      padding-top: 30px;
      >p{
        line-height: 30px;
        text-align: left;
      }
    }
  }
</style>



